@use "global" as *;

.p-mv {
  position: relative;
  height: 100vh;
}
.p-mv__img {
  width: 100%;
  height: 100vh;
}
.p-mv__img img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.p-mv__block {
  color: $white;
  writing-mode: vertical-rl;
  position: absolute;
  top: 33%;
  right: 26%;
}

.p-mv__title {
  font-size: rem(20);
  letter-spacing: 0.1em;
  line-height: 2.1;
  @include mq("md") {
    font-size: rem(34);
  }
}
