.c-hamburger {
  position: relative;
  cursor: pointer;
  max-width: rem(29);
  width: 100%;
  height: rem(20);
  z-index: 999;
  @include mq("md") {
    display: none;
  }
}
.c-hamburger.open span {
  &:nth-child(1) {
    top: rem(10);
    transform: rotate(-45deg);
  }
  &:nth-child(2) {
    opacity: 0;
  }
  &:nth-child(3) {
    top: rem(10);
    transform: rotate(45deg);
  }
}

.c-hamburger span {
  display: inline-block;
  transition: all 0.4s;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  border-radius: rem(1);
  height: rem(2);
  background-color: $white;
  max-width: rem(29);
  width: 100%;
  &:nth-child(2) {
    top: rem(10);
  }
  &:nth-child(3) {
    top: rem(20);
  }
}
